import { Table as AntdTable, Input, InputNumber, Popconfirm, Form, Typography } from 'antd';
import { useState } from 'react';
import styles from './EditableTable.module.scss';

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Введите ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const EditableTable = ({ dataSource, columns, ...restProps }) => {
  const [form] = Form.useForm();
  const [data, setData] = useState(dataSource);
  const [editingKey, setEditingKey] = useState('');

  const isEditing = (record) => record.key === editingKey;

  const edit = (record) => {
    form.setFieldsValue({
      name: '',
      ...record,
    });
    setEditingKey(record.key);
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (key) => {
    // try {
    //   const row = await form.validateFields();
    //   const newData = [...data];
    //   const index = newData.findIndex((item) => key === item.key);
    //   if (index > -1) {
    //     const item = newData[index];
    //     newData.splice(index, 1, { ...item, ...row });
    //     setData(newData);
    //     setEditingKey('');
    //   } else {
    //     newData.push(row);
    //     setData(newData);
    //     setEditingKey('');
    //   }
    // } catch (errInfo) {
    //   console.log('Validate Failed:', errInfo);
    // }
    try {
      const row = await form.validateFields();
      console.log('row:', row);
      console.log('key:', key);
      setEditingKey('');
    } catch (errInfo) {}
  };

  const handleDelete = async (key) => {};

  const columnsEdit = [
    ...columns,
    {
      title: 'Редактировать',
      dataIndex: 'operation1',
      width: '150px',
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Typography.Link
              onClick={() => save(record.key)}
              style={{
                marginRight: 8,
                color: '#818ea3',
              }}
            >
              Сохранить
            </Typography.Link>
            <Popconfirm title="Уверены что Отменить?" onConfirm={cancel} cancelText="Отмена">
              <a>Отменить</a>
            </Popconfirm>
          </span>
        ) : (
          <Typography.Link
            disabled={editingKey !== ''}
            onClick={() => edit(record)}
            style={{
              color: '#818ea3',
            }}
          >
            Редактировать
          </Typography.Link>
        );
      },
    },
    {
      title: 'Удалить',
      dataIndex: 'operation2',
      render: (_, record) => (
        <Popconfirm
          title="Уверены что Удалить?"
          onConfirm={() => handleDelete(record.key)}
          cancelText="Отмена"
        >
          <a>Удалить</a>
        </Popconfirm>
      ),
    },
  ];

  const mergedColumns = columnsEdit.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'age' ? 'number' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  console.log('mergedColumns:', mergedColumns);

  return (
    <Form form={form} component={false}>
      <AntdTable
        className={styles.table}
        components={{
          body: {
            cell: EditableCell,
          },
        }}
        dataSource={dataSource}
        columns={mergedColumns}
        rowClassName={styles.editableRow}
        {...restProps}
      />
    </Form>
  );
};

export default EditableTable;
